@charset "UTF-8";
@font-face {
  font-family: "Rostelecom Basis";
  src: url("../fonts/rostelecom-basis-reg.woff2") format("woff2"), url("../fonts/rostelecom-basis-reg.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

html {
  box-sizing: border-box;
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  display: flex;
  flex-direction: column;
  min-width: 1024px;
  max-width: 1920px;
  height: 100%;
  margin: 0 auto;
  padding: 0;
  font-family: "PT Sans", "Georgia", serif;
  color: #303030;
  background-color: #ffffff;
  overflow-x: hidden; }

img {
  max-width: 100%;
  height: auto; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

a {
  text-decoration: none; }

button {
  margin: 0;
  padding: 0;
  border: none;
  background: none;
  cursor: pointer; }

.center {
  min-width: 1024px;
  max-width: 1400px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.main {
  flex-grow: 1; }

.swiper {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-family: "Rostelecom Basis", "Ðrial", sans-serif; }

.swiper__wrapper {
  display: flex;
  height: 100%; }

.swiper__slide {
  margin: 0;
  padding: 0;
  font-size: 18px;
  line-height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  height: auto; }

.swiper__slide-3 .card__img {
  margin-left: -375px; }

.swiper__slide-4 .card__img {
  margin-left: -275px; }

.swiper__slide-1 {
  background-color: #80cbc4; }

.swiper__slide-2 {
  background-color: #e8b3ac; }

.swiper__slide-3 {
  background-color: #cacaca; }

.swiper__slide-4 {
  background-color: #9bd6e3; }

.swiper__button {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 65px;
  height: 112px;
  top: 50%;
  margin-top: -56px;
  background-image: url("../img/icons/arrow.svg");
  transition: all 0.4s ease-in; }
  .swiper__button:focus, .swiper__button:active {
    outline: none; }

.swiper-button-animate:before {
  position: absolute;
  content: '';
  display: block;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #ffffff;
  border-radius: 50%;
  transform: scale(0.001, 0.001);
  animation: swiper-button 0.8s ease-out; }

.swiper__button-prev {
  left: 72.7px; }

.swiper__button-next {
  right: 72.7px;
  transform: rotateY(180deg); }

.swiper__pagination {
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 10px;
  margin: 0;
  cursor: auto; }
  .swiper__pagination:focus, .swiper__pagination:active {
    border: none;
    outline: none; }

.swiper__pagination-bullet {
  display: inline-block;
  width: 12px;
  height: 12px;
  margin-top: 6px;
  margin-left: 24px;
  border: 1px solid #ffffff;
  cursor: pointer; }
  .swiper__pagination-bullet:first-child {
    margin-left: 0; }

.swiper__pagination-bullet-active {
  width: 24px;
  height: 24px;
  margin-top: 0; }

.swiper-horizontal > .swiper__pagination-bullets {
  width: 100%; }

@keyframes swiper-button {
  50% {
    transform: scale(1.5, 1.5);
    opacity: 0; }
  99% {
    transform: scale(0.001, 0.001);
    opacity: 0; }
  100% {
    transform: scale(0.001, 0.001);
    opacity: 1; } }

@media (min-width: 1024px) and (max-width: 1399px) {
  .swiper__button {
    width: 30px;
    height: 112px; }
  .swiper__button-prev {
    left: 20px; }
  .swiper__button-next {
    right: 20px; }
  .swiper__slide-3 .card__img {
    width: 300px;
    margin-left: -150px; }
  .swiper__slide-4 .card__img {
    width: 270px;
    margin-left: -135px; } }

.header__wrapper {
  display: flex;
  width: 100%; }

.header__logo-link {
  width: 192px;
  height: 82px;
  margin-top: 15px; }
  .header__logo-link:focus, .header__logo-link:active {
    outline: 1px solid #303030;
    outline-offset: 3px; }

.header__content {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-left: 120px; }

.bottom {
  flex-grow: 1;
  padding-top: 11px;
  padding-bottom: 13px; }

.bottom__list {
  display: flex;
  flex-wrap: wrap; }

.bottom__item {
  width: min-content;
  margin-right: 44px;
  margin-bottom: 5px; }
  .bottom__item:last-child {
    margin: 0;
    width: 215px; }

.bottom__item-link {
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 16px;
  display: block;
  color: #003883;
  font-weight: 700; }
  .bottom__item-link:focus, .bottom__item-link:active {
    outline: 1px solid #303030;
    outline-offset: 3px; }

.card {
  display: flex;
  justify-content: space-between;
  position: relative;
  width: 100%;
  height: 100%;
  color: #303030; }

.card__img {
  position: absolute;
  left: 50%;
  bottom: 0;
  margin-left: -240px; }

.card__left {
  width: 340px;
  padding-top: 88px; }

.card__left-title {
  margin: 0;
  padding: 0;
  font-size: 35px;
  line-height: 43px;
  margin-bottom: 30px;
  text-transform: uppercase; }

.card__left-text {
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 26px; }

.card__right {
  width: 395px;
  padding-top: 195px; }

.card__right-value {
  margin: 0;
  padding: 0;
  font-size: 120px;
  line-height: 120px; }

.card__right-sum {
  margin: 0;
  padding: 0;
  font-size: 48px;
  line-height: 62px; }

.card__right-text {
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 26px;
  width: 340px;
  margin-left: 15px; }

@media (min-width: 1024px) and (max-width: 1399px) {
  .card {
    padding-left: 90px;
    padding-right: 90px; }
  .card__left {
    width: 260px; }
  .card__left-title {
    margin: 0;
    padding: 0;
    font-size: 18px;
    line-height: 24px; }
  .card__left-text {
    margin: 0;
    padding: 0;
    font-size: 16px;
    line-height: 16px; }
  .card__right {
    width: 250px;
    padding-top: 125px; }
  .card__right-value {
    margin: 0;
    padding: 0;
    font-size: 24px;
    line-height: 26px; }
  .card__right-sum {
    margin: 0;
    padding: 0;
    font-size: 18px;
    line-height: 24px; }
  .card__right-text {
    margin: 0;
    padding: 0;
    font-size: 16px;
    line-height: 16px;
    width: 250px; }
  .card__img {
    width: 240px;
    margin-left: -120px; } }

.top {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgba(0, 56, 131, 0.4); }

.top__left {
  padding-top: 7px; }

.top__left-title {
  margin: 0;
  padding: 0;
  font-size: 18px;
  line-height: 24px;
  color: #0061a1; }

.top__right {
  position: relative;
  margin-bottom: -1px;
  padding: 9px 15px 5px 40px;
  z-index: 1;
  background: linear-gradient(270deg, #003883 0%, #0061a1 97.05%);
  border-bottom: 1px solid #003883; }
  .top__right::before {
    position: absolute;
    content: "";
    left: 0;
    top: 0;
    width: 0;
    height: 0;
    border-top: 0px solid transparent;
    border-left: 15px solid #ffffff;
    border-bottom: 40px solid transparent; }
  .top__right::after {
    position: absolute;
    content: "";
    display: block;
    width: 5000px;
    height: calc(100% + 1px);
    right: -5000px;
    top: 0;
    z-index: 0;
    background: #003883; }

.top__right-list {
  display: flex;
  align-items: center;
  height: 25px; }

.top__right-item {
  display: flex;
  margin-left: 35px; }
  .top__right-item:first-child {
    margin: 0; }

.top__right-input {
  width: 0;
  height: 22px;
  margin-right: 5px;
  padding: 0;
  border: 0;
  box-shadow: none;
  background: #ffffff;
  line-height: 22px;
  transition: width 0.2s, padding 0.2s, margin-right 0.2s; }
  .top__right-input--active {
    width: 150px;
    margin-right: 15px;
    padding-left: 5px;
    padding-right: 5px; }

.top__right-button {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 25px;
  color: #ffffff; }
  .top__right-button:focus, .top__right-button:active {
    border: none;
    outline: none; }

.top__right-button--search {
  width: 22px;
  height: 21px;
  background-image: url("../img/icons/search.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain; }

.top__right-button--settings {
  width: 19px;
  height: 22px;
  background-image: url("../img/icons/settings.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain; }

.top__right-button--save,
.top__right-button--language {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 25px; }
